import uniGrid from '../../components/uni-grid/uni-grid.vue';
import uniGridItem from '../../components/uni-grid-item/uni-grid-item.vue';
export default {
	components: {
		uniGrid,
		uniGridItem
	},
	props: [
		'item'
	],
	methods: {
		previewImage(index) {
			uni.previewImage({
				current: index,
				indicator: 'default',
				urls: this.item.imageList
			});
		},
		attention(isAttention) {
			this.isAttention = !isAttention;
			uni.showToast({
				title: '关注成功'
			});
		}
	}
}
